import React from "react";
import { Switch, Route } from "react-router-dom";

import Invoices from "../Components/Invoices/Invoices";

const main = props => (
  <Switch>
    <Route
      path="/"
      render={props => <Invoices {...props} invoices={props.invoices} />}
    />
  </Switch>
);

export default main;



